*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%; //defines 1rem = 10px; 10/16 = 62.5%
}

body {
  box-sizing: border-box;
  min-height: 100vh; //testing
}

a {
  outline: none;
}



